import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { FaFilter } from "react-icons/fa";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import { toast } from "react-toastify";

import subjectGoalAPI from "api/common/subject-goal";

export default function TableFilter({
  currentFilter,
  handleSubjectGoalFilterOnChange,
  handleFilterOnClick,
}) {
  const { t } = useTranslation();

  const [subjectGoals, setSubjectGoals] = useState([]);

  useEffect(async () => {
    try {
      const subjectGoalsRes = await subjectGoalAPI.getAll();
      setSubjectGoals(subjectGoalsRes.contents);
      
    } catch (err) {// eslint-disable-next-line
    }
  }, []);

  return (
    <Form className="d-flex flex-wrap">
      <FormGroup>
        <Label for="subjectGoal">
          {t("admin.subject_output_standard_list.SUBJECT_GOAL")}
        </Label>
        <Input
          type="select"
          value={
            currentFilter.subjectGoal !== null ? currentFilter.subjectGoal : -1
          }
          onChange={handleSubjectGoalFilterOnChange}
        >
          <option value={-1}>{t("admin.common.ALL")}</option>
          {subjectGoals &&
            subjectGoals.map((item, index) => {
              return (
                <option key={index} value={item.id}>
                  {item.ma}
                </option>
              );
            })}
        </Input>
      </FormGroup>
      &emsp;
      <FormGroup className="align-self-end">
        <Button onClick={handleFilterOnClick}>
          <FaFilter /> {t("admin.common.FILTER")}
        </Button>
      </FormGroup>
    </Form>
  );
}
